@import "project.less";
@import "smart-grid.less";

@main_color: #ffffff;
@main_img: url(/img/header.png) no-repeat #0bb4bd;
@pad_btn: 10px 25px;
@rad_btn: 5px;
@font_btn: 15px;
@item_margin: 95px;
@item_h2: 45px;
@color_h: #061e37;
@color_p: #78909c;
@useful_margin: 20px;
@pad_testimonials:35px;
@our_input:200px;

.reset();

.wrapper {
  .wrapper();
  background-color: transparent;
}



/*стили под модальное окно*/
.overlay{
  background-color: #131304;
  opacity: .6;
  position: fixed;
  width: 100%;
  bottom: 0;
  top: 0;
  z-index: 1000;
  display: none;
}
.overflow{
  overflow: hidden;
}
.modal{
  background-color:@main_color;
  color: @color_p;
  width: 400px;
  min-height: 250px;
  border-radius: @rad_btn;
  position: absolute;
  top:-200px;
  left: 50%;
  margin-left: -200px;
  z-index: 1001;
  display: none;
  padding: 15px;
  &>*{
    margin-bottom: 15px;
  }
  &>.close{
    position: absolute;

    right: 4px;
    top: 4px;
    border: none;
    padding: 0;
    background-color: transparent;
    font-size: 18px;
    &:hover{
      background-color:transparent;
      color:@color_h;
    }
  }


  p:first-child{
    text-align: center;
    font-size: 1.8rem;
  }
  input,select{
    display: block;
    width: 100%;
    border-radius: @rad_btn;
    height: 35px;
    line-height: 35px;
  }
  textarea{
    display: block;
    width: 100%;
    border-radius: @rad_btn;
    margin-top: 25px;
  }

  label{
    display: block;
    span{
      font-weight: bold;
      display: block;
      margin-bottom: 5px;
      color:@color_h;
    }

  }
  p{
      text-align: center;
      padding-top: 10px;
      a{
        text-decoration: none;
        color:rgba(0, 70, 119, 0.9);
        font-weight: bold;
      }
  }
  button{
    display: block;
    margin:0 auto;
    border-radius: @rad_btn;
    padding: @pad_btn;
    font-size: @font_btn;
    &:hover{
      background-color: #061e37;
      color: @main_color;
      border-color: transparent;
    }

  }

}
.modal.open,
.overlay.open{
  display: block;
  transition: all .5s;
}
.modal.modal-lg{
  left: 50%;
  width: 600px;
  margin-left: -300px;
}


/*стили под модальное окно*/

.debug {
  .debug(#cccccc9c, 1px solid #ccc);
}

h2, .h2 {
  font-size: 2.8rem;
  text-align: center;
  margin: 20px 0;
  color: @color_h;
}

h4 {
  font-size: 20px;
  color: @color_h;
}

p {
  font-size: 18px;
  color: @color_p;
}

button {
  cursor: pointer;
  font-size: @font_btn;
}
.bold{
  font-weight: 600;
  color:#000000;
}
.items {
  margin-top: @item_margin;
  margin-bottom: @item_margin;
}

header {
  background: @main_img;
  color: @main_color;

  .header-top {
    max-width: 1440px;
    padding-top: 40px;
    margin: 0 auto;
    display: flex;

    justify-content: space-between;
    align-items: center;

    nav ul {
      display: flex;

      li {
        margin-left: 15px;

        &:hover {
          color: #000000;
          cursor: pointer;
        }
      }
    }

    .logo {
      display: flex;

      svg {
        flex-shrink: 0;
      }
    }

    button {
      border-radius: @rad_btn;
      padding: @pad_btn;
      background-color: transparent;
      border: 1px solid #fff;
      color: @main_color;
    }
  }

  .items {
    margin-top: ~"calc(@{item_margin} - 10px)";
    padding-bottom: ~"calc(@{item_margin} - 10px)";
    .md-block({
        padding:0;
        margin-top:10px;
      });
    .row-flex();

    .item {
      .col();
      .size(12);
      .size-md(24);
      .align-items-md(center);
      display: flex;
      align-items: start;
      flex-direction: column;



      h1 {
        text-transform: uppercase;
        font-size: 55px;
        .md-block({
          text-align: center;
          margin-top: 15px;
          })
      }

      p {
        font-size: 18px;
        color: #ffffff;
      }

      button {
        padding: @pad_btn;
        border-radius: @rad_btn;
        background-color: @main_color;

        &:hover {
          background-color: #061e37;
          color: @main_color;
          border-color: transparent;
        }
      }

      div {
        margin: auto auto 25px 0px;
        .md-block({
          margin:25px auto 25px auto;
          })
      }
    }
  }

img{
  .md(display,none)
}
}

section.easy_useful {
  p {
    text-align: center;
  }

  .items {
    .row-flex;

    .item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      .col();
      .size(8);
      .size-xs(24);
      .xs(margin-bottom,20px);

      img {
        margin-bottom: ~"calc(@{useful_margin} * 2)";
      }

      h4 {
        margin-bottom: @useful_margin;
      }

    }
  }

  .get_started {
    padding: @pad_btn;
    border-radius: @rad_btn;
    font-size: @font_btn;
    color: @main_color;
    background-color: #1de9b6;
    text-align: center;
    display: block;
    margin: 0 auto;
    border-color: transparent;

    &:hover {
      background-color: #1ed4a6;
    }
  }

}

.counts {
    .md-block({
    background: none;
    });

  background: linear-gradient(to left, #ea80fc, 50%, #80d8ff);

    .items {
      align-items: stretch;
      justify-content: center;
      min-height: 150px;
      .row-flex();
      .justify-content-md(center);
      .flex-direction-md(column);
      .align-items-md(center);




      .item {
        .col();
        .size(6);
        .size-md(20);
        color: #ffffff;
        display: flex;
        justify-content: center;
        margin:0;

        img {
          margin: auto 10px auto 0;
        }

        span {
          margin: auto 10px auto;
        }
      }
      .item:first-child{
        background-color: #80d8ff;
      }
      .item:nth-child(2){
        background-color: #82b1ff;
      }
      .item:nth-child(3){
        background-color: #b388ff;
      }
      .item:nth-child(4){
        background-color: #ea80fc;
      }

  }

}

.services p {
  text-align: center;
}
.services h2+p{
  margin-bottom:50px;
}
.accordion .panel-default {

  box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
.panel-heading,.panel-collapse{
  padding: 0 25px 25px 10px;
}
  .panel-heading {
    text-transform: uppercase;

    h4,i {
      line-height: 54px;
    }
    h4 .fa-angle-down {
      float: right;
    }


  }
  .panel-collapse .panel-body{

  }

  .panel-body {
    .flex-direction-sm(column);
    .md-block({
      justify-content: space-between;
      content:aaa;
      });
    display: flex;
    justify-content: center;

    p {
      flex-grow: 3;
      margin-bottom: 25px
    }

    img {
      flex-basis: 250px;
      .flex-basis-md(450px);
      .flex-basis-sm(70%);
      .sm(margin-bottom,30px)

    }
  }
}

.testimonials {
  background: @main_img;

  h2 {
    text-align: left;
    color: @main_color;
  }
  h2,p{
    .md(text-align,center)
  }


  button {
    border-radius: @rad_btn;
    padding: @pad_btn;
    background-color: @main_color;
    border: 1px solid #fff;
    margin: auto auto 95px 0;
    .md(margin,25px auto);

    &:hover {
      background-color: #061e37;
      color: @main_color;
      border-color: transparent;
    }
  }

  .items {
    .row-flex()
  }

  .item {
    .col();
    .size(12);
    .size-md(24);
    display: flex;
    flex-direction: column;
      &:first-child{
        color:@main_color;
      }
      &:last-child > div{
        display: flex;
        align-items: center;
        margin:20px auto;
        p{
          background: @main_color;
          border-radius: @rad_btn;
          padding: @pad_testimonials ;
          .bold{
            display: block;
            margin-bottom: 20px;
          }
        }
        img{
          flex-basis: 40%;
          min-width: 105px;
        }
      }


  }
}
.our_team{
