  .flex_input{
    display: flex;
    flex-wrap: wrap;
      &:first-child{
        flex-wrap: wrap;
        flex-basis: 200px;
        justify-content: space-between;
      }
    input,textarea{
      flex-grow: 1;
      padding: @pad_btn;
      border-radius: @rad_btn;
      font-size: @font_btn;
      box-sizing: border-box;
      margin: 15px;
    }
  }
  label,p,h2{
    margin: auto 15px;
  }
  label{
    float: left;
    line-height: 50px;
    input{
      margin-right: 10px;
