    color: @main_color;
  }

  .footer_top,.footer_bottom{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin:35px auto;

    .flex-direction-sm(column);
    .align-content-sm(center);
    .align-items-sm(center);
