body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1440px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  background-color: transparent;
}

/*стили под модальное окно*/

.overlay {
  background-color: #131304;
  opacity: .6;
  position: fixed;
  width: 100%;
  bottom: 0;
  top: 0;
  z-index: 1000;
  display: none;
}

.overflow {
  overflow: hidden;
}

.modal {
  background-color: #ffffff;
  color: #78909c;
  width: 400px;
  min-height: 250px;
  border-radius: 5px;
  position: absolute;
  top: -200px;
  left: 50%;
  margin-left: -200px;
  z-index: 1001;
  display: none;
  padding: 15px;
}

.modal > * {
  margin-bottom: 15px;
}

.modal > .close {
  position: absolute;
  right: 4px;
  top: 4px;
  border: none;
  padding: 0;
  background-color: transparent;
  font-size: 18px;
}

.modal > .close:hover {
  background-color: transparent;
  color: #061e37;
}

.modal p:first-child {
  text-align: center;
  font-size: 1.8rem;
}

.modal input,
.modal select {
  display: block;
  width: 100%;
  border-radius: 5px;
  height: 35px;
  line-height: 35px;
}

.modal textarea {
  display: block;
  width: 100%;
  border-radius: 5px;
  margin-top: 25px;
}

.modal label {
  display: block;
}

.modal label span {
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
  color: #061e37;
}

.modal p {
  text-align: center;
  padding-top: 10px;
}

.modal p a {
  text-decoration: none;
  color: rgba(0, 70, 119, 0.9);
  font-weight: bold;
}

.modal button {
  display: block;
  margin: 0 auto;
  border-radius: 5px;
  padding: 10px 25px;
  font-size: 15px;
}

.modal button:hover {
  background-color: #061e37;
  color: #ffffff;
  border-color: transparent;
}

.modal.open,
.overlay.open {
  display: block;
  transition: all .5s;
}

.modal.modal-lg {
  left: 50%;
  width: 600px;
  margin-left: -300px;
}

/*стили под модальное окно*/

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1440px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  background-color: transparent;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(4.166666666666667% - 32px);
  height: inherit;
  outline: 1px solid #ccc;
  background: #cccccc9c 9c;
}

h2,
.h2 {
  font-size: 2.8rem;
  text-align: center;
  margin: 20px 0;
  color: #061e37;
}

h4 {
  font-size: 20px;
  color: #061e37;
}

p {
  font-size: 18px;
  color: #78909c;
}

button {
  cursor: pointer;
  font-size: 15px;
}

.bold {
  font-weight: 600;
  color: #000000;
}

.items {
  margin-top: 95px;
  margin-bottom: 95px;
}

header {
