  color: #ffffff;
}

header .header-top {
  max-width: 1440px;
  padding-top: 40px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header .header-top nav ul {
  display: flex;
}

header .header-top nav ul li {
  margin-left: 15px;
}

header .header-top nav ul li:hover {
  color: #000000;
  cursor: pointer;
}

header .header-top .logo {
  display: flex;
}

header .header-top .logo svg {
  flex-shrink: 0;
}

header .header-top button {
  border-radius: 5px;
  padding: 10px 25px;
  background-color: transparent;
  border: 1px solid #fff;
  color: #ffffff;
}

header .items {
  margin-top: calc(95px - 10px);
  padding-bottom: calc(95px - 10px);
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
}

header .items .item {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(50% - 32px);
  display: flex;
  align-items: start;
  flex-direction: column;
}

header .items .item h1 {
  text-transform: uppercase;
  font-size: 55px;
}

header .items .item p {
  font-size: 18px;
  color: #ffffff;
}

header .items .item button {
  padding: 10px 25px;
  border-radius: 5px;
  background-color: #ffffff;
}

header .items .item button:hover {
  background-color: #061e37;
  color: #ffffff;
  border-color: transparent;
}

header .items .item div {
  margin: auto auto 25px 0px;
}

section.easy_useful p {
  text-align: center;
}

section.easy_useful .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
}

section.easy_useful .items .item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 32px);
}

section.easy_useful .items .item img {
  margin-bottom: calc(20px * 2);
}

section.easy_useful .items .item h4 {
  margin-bottom: 20px;
}

section.easy_useful .get_started {
  padding: 10px 25px;
  border-radius: 5px;
  font-size: 15px;
  color: #ffffff;
  background-color: #1de9b6;
  text-align: center;
  display: block;
  margin: 0 auto;
  border-color: transparent;
}

section.easy_useful .get_started:hover {
  background-color: #1ed4a6;
}

.counts {
  background: linear-gradient(to left, #ea80fc, 50%, #80d8ff);
}

.counts .items {
  align-items: stretch;
  justify-content: center;
  min-height: 150px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
}

.counts .items .item {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(25% - 32px);
  color: #ffffff;
  display: flex;
  justify-content: center;
  margin: 0;
}

.counts .items .item img {
  margin: auto 10px auto 0;
}

.counts .items .item span {
  margin: auto 10px auto;
}

.counts .items .item:first-child {
  background-color: #80d8ff;
}

.counts .items .item:nth-child(2) {
  background-color: #82b1ff;
}

.counts .items .item:nth-child(3) {
  background-color: #b388ff;
}

.counts .items .item:nth-child(4) {
  background-color: #ea80fc;
}

.services p {
  text-align: center;
}

.services h2 + p {
  margin-bottom: 50px;
}

.accordion .panel-default {
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
}

.accordion .panel-default .panel-heading,
.accordion .panel-default .panel-collapse {
  padding: 0 25px 25px 10px;
}

.accordion .panel-default .panel-heading {
  text-transform: uppercase;
}

.accordion .panel-default .panel-heading h4,
.accordion .panel-default .panel-heading i {
  line-height: 54px;
}

.accordion .panel-default .panel-heading h4 .fa-angle-down {
  float: right;
}

.accordion .panel-default .panel-body {
  display: flex;
  justify-content: center;
}

.accordion .panel-default .panel-body p {
  flex-grow: 3;
  margin-bottom: 25px;
}

.accordion .panel-default .panel-body img {
  flex-basis: 250px;
}

.testimonials {
