  display: flex;
  flex-direction: column;
}

.testimonials .item:first-child {
  color: #ffffff;
}

.testimonials .item:last-child > div {
  display: flex;
  align-items: center;
  margin: 20px auto;
}

.testimonials .item:last-child > div p {
