  }

  footer .footer_top,
  footer .footer_bottom {
    align-content: center;
  }

  footer .footer_top,
  footer .footer_bottom {
    align-items: center;
  }

  footer .footer_top > *,
  footer .footer_bottom > * {
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .debug > div {
    padding-left: 15px;
    padding-right: 15px;
  }

  .debug > div {
    padding-left: 15px;
    padding-right: 15px;
  }

  section.easy_useful .items .item {
    width: calc(100% - 32px);
  }

  section.easy_useful .items .item {
    margin-bottom: 20px;
  }
}